.catalog {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__inner {
 }    // width: 1100px

  &__title {
    display: none; }

  &__content {
    display: flex;
 }    // justify-content: center

  &__cards {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: $screen-m) {
      flex-direction: row;
      flex-wrap: wrap; } }

  &__pagination {
    display: flex;
    justify-content: space-around; } }
